          solo
          v-model="focused_log"
          item-value="name"
          style="max-width: 250px; border-right: 1px solid rgb(55,55,55); border-radius: 0;"
          hide-details
          label="Log History"
        ></v-select>
          <v-text-field label="Search log" dense solo flat color="neon" hide-details v-model="log_search"
          style="background: transparent;flex:1" prepend-inner-icon="mdi-magnify" height="40"></v-text-field>
          <v-btn @click="SaveLog" small  depressed style="text-transform: capitalize; margin:auto;" class="mr-2">Save Log</v-btn>
        </div>
      
        <v-divider></v-divider>
        <v-tabs-items id="console-ui" v-model="log_tab" style="flex:auto; overflow: auto;">
          <v-tab-item v-for="item in log_category" :key="item" transition="fade-transition">
