              <v-list-item v-for="(log, i) in display_log" :key="i">
               
                <div style="display: flex;" >
                  <div style="min-width:80px"> {{ log.timestamp }}</div>
                  <div> {{ log.description }}</div>
                </div>
              </v-list-item>
