        </div>
      </v-main>
    </v-app>
  </div>
</template>
<script>
import "@/style.scss"
const params = new URL(window.location).searchParams
const procedure = async (data) => {
  try {
    await window.chrome.webview.postMessage(data)
  }
  catch (ex) {
    console.log(ex)
  }
}
const routines = [
  {
    category: "Simple Task",
    list: [
      "Spin Coin",
      "Synth Gems",
      "Opening Pack"
    ]
  },
  {
    category: "Dailies",
    list: [
      "Corruption",
      "Ladder",
      "Bath",
    ]
  },
  {
    category: "Grind Gold",
    list: [
      "310", "320", "340", "355"]
  },
  {
    category: "Dungeons",
    list: [
      "30",
      "45",
      "55",
      "75",
      "85",
      "110",
      "125",
      "165",
      "175",
      "195",
      "215",
      "235",
      "255",
      "275",
      "295",
      "315",
      "335",
      "355",
      "375",
      "395",
      "415",
      "Pixie"
    ]
  }
]


export default {
  data: () => ({
    ready: false,
    character: {
      nickname: "",
      routines: [],
      done: []
    },
    is_macro_running: false,

    routines,
    search_routine: "",
    shortcuts: [
      { type: "dungeons", name: "D85" }
    ],
    selected_macro: "",
    workspace_mode: params.get("workspace_mode") || 'freemode',
    flash_hwnd: params.get("flash_hwnd") || 0,

    log_row_limit: 300,
    log_category: ['Event', 'Image', 'Error', 'Macro', 'Test', 'All'],
    log_tabs_icon:[ 'mdi-book-clock-outline', 'mdi-image-outline', 'mdi-alert-circle-outline', 'mdi-robot-outline', 'mdi-progress-check', 'mdi-format-list-checkbox'],
    log_tab: 0,
    log_search: "",
    logs:[],
    focused_log: null
    

    
  }),
  watch: {},
  computed: {
    filtered_routines: function () {
      console.log(this.routines)
      let r = this.routines.map((r) => ({
        category: r.category,
        list: r.list.filter(l => l.toLowerCase().includes(this.search_routine))
      })).filter((r) => r.list.length)
      console.log(r)
      return r
    },
    progress() {
      return `${this.character.done.length} / ${this.character.routines.length}`
    },
    display_log() {
      

      if(this.focused_log == null) return
      let log_index = this.logs.findIndex((l) => l.name == this.focused_log)
     
      let records = (JSON.parse( JSON.stringify( this.logs[log_index].records)))
      console.log( log_index,  records, this.logs, this.focused_log)
      // console.log(log_index, records)
       if(this.log_category[this.log_tab] == "All"){
        records = records
        .filter((l) => {
          return  (`${l.timestamp}:${l.category}:${l.description}`).toLowerCase().includes(this.log_search.toLocaleLowerCase())
        })
        .map((l) => {
          l.description = `${l.category}:${l.description}`
          return l
        })
       
       }else{
        records = records.filter((l) => {
          return l.category == this.log_category[this.log_tab] &&
            (l.timestamp + " " + l.description).toLowerCase().includes(this.log_search.toLocaleLowerCase())
        })
      }
      return  records.slice(this.log_row_limit * -1)
    }
  },
  
  methods: {

    SetCharacter(character) {
      this.character = { ...this.character, ...character}
      console.log("SETTING CHARACTER", this.character)
      this.ready = true
    },
    CopyFlashHWND() {
      navigator.clipboard.writeText(this.flash_hwnd)
    },
    Screenshot() {
      procedure({ Event: "screenshot", Data: null })
    },
    Automate() {
      this.RunMacro("..", "Automate")
      // procedure({ Event: "run_macro", Data: `..\\automate` })
  
    },
    Test() {
      this.RunMacro("..", "Test")
    },

    SaveLog(){
      var textToSave =  JSON.stringify(this.logs, null, 2);
      var hiddenElement = document.createElement('a');
      hiddenElement.href = 'data:attachment/json,' + encodeURI(textToSave);
      hiddenElement.target = '_blank';
      hiddenElement.download = 'myFile.json';
      hiddenElement.click();
    },
    Log(log_obj) {
      let log_index = this.logs.findIndex((l) => l.name == this.focused_log)
      // console.l
      this.logs[log_index].records.push({
        ...log_obj,
        timestamp: new Date().toString().slice(15, 24)
      })
      this.ScrollToLatest()

    },
    ScrollToLatest() {
      var objDiv = document.getElementById("console-ui");
      setTimeout(() => {
        objDiv.scrollTop = objDiv.scrollHeight;
      }, 300)

    },
    RunMacro(category, routine) {
      console.log(category, routine)
      this.is_macro_running = true
      this.selected_macro = routine

      const log_section = {
        name: `${new Date().toString().slice(16, 24)}-${routine}`,
        timestamp: new Date().toString().slice(16, 24),
        routine_name: routine,
        records: []
      }
      this.logs.push(log_section)
      this.focused_log = log_section.name
      procedure({ Event: "run_macro", Data: encodeURI(`${category}/${routine}`) })
    },
    StopMacro() {
      this.is_macro_running = false
      this.selected_macro = ""
      procedure({ Event: "stop_macro", Data: null })
    }
  },
  mounted() {
    window.$com = this
    if (this.workspace_mode == 'freemode') {
      this.shortcuts.splice(0, 0, { type: "..", name: "Automate" })
    }
    this.SetCharacter({
      nickname:"ArikaStevany"
    })
  }

}
</script>